/* ActionButton component stylesheet */

/* Colors stylsheet */

:root {
    --black: #000;
    --darkGray: #3E3E3E; 
    --gray: #6C6C6C;
    --lightGray: gray;
    --red: #EF0000;
    --white: #FFF;
}

._20CtX {
    align-items: center;
    color: #3E3E3E;
    color: var(--darkGray);
    cursor: pointer;
    display: inline-flex;
    font-size: 1.2em;
    font-weight: 500;
    padding: 7px;
    padding-left: 0;
}

._20CtX:hover {
    color: #000;
    color: var(--black);
}

._20CtX svg {
    padding-right: 7px;
}

._20CtX label {
    cursor: pointer;
}

._144r7 {
    color: gray;
    color: var(--lightGray);
}
